<template>
    <div class="container">
        <div class="money-info-page">
            <div class="pict-layout">
                <img src="../../static/img/b19204_217x136.png" alt="">
            </div>
            <div class="cutdown">
                <div v-if="query_status.status == 0">
                    <div class="time">请在 <span>{{downTime | CountDown(true)}}</span> 内完成汇款</div>
                    <p>如遇风控提示：本次交易存在风险，可继续付款</p>
                </div>
                <div v-if="query_status.status == 1">
                    <h2 class="success">充值成功</h2>
                </div>
                <div v-if="query_status.status == 2">
                    <h2 class="error1">充值失败</h2>
                </div>
            </div>
            <div class="kaihu">
                <ul>
                    <li>
                        <span>开户人：</span>
                        <h3>{{bankcardList.userName}}</h3>
                        <b class="fr"
                        v-clipboard:copy="bankcardList.userName" 
                        v-clipboard:success="copy"
                        >复制</b>
                    </li>
                    <li>
                        <span>银行卡号：</span>
                        <h3>{{bankcardList.bankAccount}}</h3>
                        <b class="fr"
                        v-clipboard:copy="bankcardList.bankAccount" 
                        v-clipboard:success="copy"
                        >复制</b>
                    </li>
                    <li>
                        <span>开户行：</span>
                        <h3>{{bankcardList.bankName}}</h3>
                        <b class="fr"
                        v-clipboard:copy="bankcardList.bankName" 
                        v-clipboard:success="copy"
                        >复制</b>
                    </li>
                    <li>
                        <span>支付金额：</span>
                        <h3 class="color">{{money}}</h3>
                    </li>
                </ul>
            </div>
            <div class="qinai">
                <p class="color">注意事项：</p>
                <p>1.请勿保留旧的银行卡号进行交易，否则将会导致资金无法到账，交易完成后请直接关闭页面即可。如若入款至过期账号，本网站无法查收会导致不能自动到账。</p>
                <p>2.转账付款完成后：10分钟内自动到账。超过10分钟未到账请联系在线客服团队。</p>
                <p>3.修改金额和重复支付将导致不能自动到账。</p>
                <button @click="close">汇款后，点此关闭</button>
            </div>
        </div>
        <div class="kefu">
            <img src="@/static/img/f449f2_112x94.png" alt="">
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                downTime: 600,
                timer1: null,

                bankcardList: {},
                DepositList: [],
                time: null,

                query_status: {
                    status: 0,
                }
            }
        },
        computed:{
            ...mapState('Recharge', {
                user: state => state.user,
                money: state => state.money,
            }),
        },
        created(){
            this.getSystemBankCard();
            this.getDepositList();
        },
        methods: {
            // 获取银行卡
            getSystemBankCard(){
                this.$api.recharge.getSystemBankCard(this.user)
                .then(res=>{
                    console.log(res);
                    this.bankcardList = res.bankcardList[0]
                    
                })
            },
            copy(){
                this.$toast('复制成功!')
            },
            getDepositList(){
                this.$api.user.getDepositList(this.user)
                .then(res=>{
                    console.log(res);
                    this.DepositList = res.userDepositList;
                    this.downTimefun();
                    this.getDeposit();
                    this.time = setInterval(this.getDeposit, 3000);
                })
            },
            downTimefun(){
                this.downTime = this.downTime - 1
                this.timer1 = setInterval(()=>{
                    this.downTime = this.downTime - 1
                    if(this.downTime <= 0){
                        clearInterval(this.timer1);
                    }
                }, 1000)
            },
            // 单个查询记录
            getDeposit(){
                let data = {
                    ...this.user,
                    orderNo: this.DepositList[0].orderNo
                }
                this.$api.user.getDeposit(data)
                .then(res=>{
                    console.log(res);
                    this.query_status = res
                    if(res.status >= 1){
                        clearInterval(this.time);
                        clearInterval(this.timer1);
                    }
                })
            },
            close(){
                this.$router.go(-2)
            }
        },
        destroyed(){
            clearInterval(this.time);
            console.log('destroyed');
        }
    }
</script>

<style lang="stylus" scoped>
    .kefu
        position fixed
        right 20px
        bottom 100px
        img
            width 60px
</style>